import React, { Component } from 'react';
import { Container, Row } from 'react-bootstrap';
import _ from 'lodash';

import Map from './Map';
import MoraleWrapper from './MoraleWrapper';

export default class RaidMap extends Component {
	constructor(props) {
		super(props);

		this.state = {
			morale: [70, 70, 70, 70],
			value: [0, 0, 0, 0],
			currentEntry: 0,
			prevEntry: 0
		};
	}

	changeMorale = (morale, operation, index) => {
		const newMorale = [...this.state.morale];
		if (operation === '+') newMorale[index] += morale;
		else newMorale[index] -= morale;

		this.setState({
			morale: newMorale
		});
	};

	resetMorale = () => {
		this.setState({
			morale: _.map(this.state.morale, () => {
				return 70;
			})
		});
	};

	changeEntry = index => {
		this.setState(prevState => ({
			prevEntry: prevState.currentEntry,
			currentEntry: index
		}));
		// this.setState({
		// 	prevEntry: this.state.currentEntry,
		// 	currentEntry: index
		// });
	};

	render() {
		return (
			<Container style={{ marginTop: '2em' }}>
				<Row>
					<Map
						changeMorale={this.changeMorale}
						index={this.state.currentEntry}
						resetMorale={this.resetMorale}
						prevIndex={this.state.prevEntry}
					/>
					<MoraleWrapper
						morale={this.state.morale}
						value={this.state.value}
						changeMorale={this.changeMorale}
						entries={this.state.morale.length}
						index={this.state.currentEntry}
						changeEntry={this.changeEntry}
					/>
				</Row>
			</Container>
		);
	}
}
