import React, { Component } from 'react';
import _ from 'lodash';
import Block from './Block';
import { Col } from 'react-bootstrap';

import { RAID_MAP, START_POSITION } from '../../constants/map';

export default class Map extends Component {
	constructor(props) {
		super(props);
		this.state = {
			map: this.copyOriginalMap(),
			cur_position: [
				START_POSITION,
				START_POSITION,
				START_POSITION,
				START_POSITION
			]
		};
	}

	isValidMove = cur_position => {
		const { index } = this.props;
		const validMoves = [[-1, 0], [1, 0], [0, -1], [0, 1]];
		for (let i = 0; i < validMoves.length; ++i)
			if (
				this.state.cur_position[index][0] + validMoves[i][0] ===
					cur_position[0] &&
				this.state.cur_position[index][1] + validMoves[i][1] ===
					cur_position[1] &&
				this.state.map[cur_position[0]][cur_position[1]] !== 0
			)
				return true;

		if (
			cur_position[0] === START_POSITION[0] &&
			cur_position[1] === START_POSITION[1] &&
			(cur_position[0] !== this.state.cur_position[index][0] ||
				cur_position[1] !== this.state.cur_position[index][1])
		)
			this.teleport();
		return false;
	};

	movePosition = position => {
		const { index } = this.props;
		const cur_position = position.split(' ').map(Number);
		if (!this.isValidMove(cur_position)) return;

		let value = 1;
		const curNodeValue = this.state.map[cur_position[0]][cur_position[1]];
		if (curNodeValue > 7) value = 8;
		else if (curNodeValue === -8) value = 7;

		const last_position = this.state.cur_position[index];
		const newPosition = [...this.state.cur_position];
		newPosition[index] = cur_position;

		this.setState({
			map: this.displayCurrentPosition(cur_position, last_position),
			cur_position: newPosition
		});

		this.props.changeMorale(value, '-', index);
	};

	teleport = () => {
		const { index } = this.props;
		const last_position = this.state.cur_position[index];
		const newPosition = [...this.state.cur_position];
		newPosition[index] = START_POSITION;
		this.setState({
			map: this.displayCurrentPosition(START_POSITION, last_position),
			cur_position: newPosition
		});

		this.props.changeMorale(3, '-', index);
	};

	displayCurrentPosition = (cur_position, last_position) => {
		const { index } = this.props;
		let temp = [...this.state.map];
		// temp[this.state.cur_position[index][0]][this.state.cur_position[index][1]] =
		temp[last_position[0]][last_position[1]] = 4 + index;
		temp[cur_position[0]][cur_position[1]] = 2;

		return temp;
	};

	refreshMap = () => {
		this.setState({
			map: this.copyOriginalMap(),
			cur_position: [
				START_POSITION,
				START_POSITION,
				START_POSITION,
				START_POSITION
			]
		});
		this.props.resetMorale();
	};

	copyOriginalMap = () => {
		return _.map(RAID_MAP, row => {
			return [...row];
		});
	};

	drawMap = () => {
		return (
			<div style={{ display: 'flex', flexDirection: 'column' }}>
				{_.map(this.state.map, (row, row_key) => {
					return (
						<div
							key={row_key}
							style={{ display: 'flex', flexDirection: 'row' }}>
							{_.map(row, (square_value, key) => {
								const index = `${row_key} ${key}`;
								return (
									<Block
										key={index}
										square_value={square_value}
										clickFunction={() => this.movePosition(index)}
									/>
								);
							})}
						</div>
					);
				})}
			</div>
		);
	};

	// componentWillReceiveProps() {
	// 	// console.log(this.props.prevIndex);
	// 	// console.log(this.props.index);
	// 	if (this.props.prevIndex !== this.props.index)
	// 		this.displayCurrentPosition(
	// 			this.state.cur_position[this.props.index],
	// 			this.state.cur_position[this.props.prevIndex]
	// 		);
	// }

	render() {
		return (
			<Col>
				{this.drawMap()}
				<button onClick={() => this.teleport()}>Teleport</button>
				<button onClick={() => this.refreshMap()}>Reset Map</button>
			</Col>
		);
	}
}
