import React, { Component } from 'react';
import { Col, Row } from 'react-bootstrap';
import _ from 'lodash';

import Morale from './Morale';

export default class MoraleWrapper extends Component {
	generateEntries = () => {
		return (
			<Col>
				{_.map(this.props.morale, (val, key) => {
					return (
						<Row
							key={key}
							onClick={() => {
								this.props.changeEntry(key);
							}}
							style={{
								border: this.props.index === key ? '2px solid black' : ''
							}}>
							<Morale
								index={key}
								// prevIndex={this.props.prevIndex}
								morale={val}
								value={this.props.value[key]}
								changeMorale={this.props.changeMorale}
								color={
									key === 0
										? 'lightblue'
										: key === 1
										? 'lightpink'
										: key === 2
										? 'lightseagreen'
										: 'lightgreen'
								}
							/>
						</Row>
					);
				})}
			</Col>
		);
	};

	render() {
		return (
			<Col>
				<h3>Number of Entries: {this.props.entries}</h3>
				{this.generateEntries()}
			</Col>
		);
	}
}
