import React from 'react';
import '../../styles/block.css';

const block = ({ square_value, clickFunction }) => {
	let block_color;
	if (square_value === 0) block_color = 'black';
	else if (square_value === 1) block_color = 'grey';
	else if (square_value === 2) block_color = 'white';
	else if (square_value === 3) block_color = 'green';
	else if (square_value === 4) block_color = 'lightblue';
	else if (square_value === 5) block_color = 'lightpink';
	else if (square_value === 6) block_color = 'lightseagreen';
	else if (square_value === 7) block_color = 'lightgreen';
	else if (square_value === 8) block_color = 'yellow';
	else if (square_value === 9) block_color = 'orange';
	else if (square_value === -8) block_color = 'salmon';
	else block_color = 'red';

	return (
		<div
			className='square'
			style={{ backgroundColor: block_color }}
			onClick={clickFunction}
		/>
	);
};

export default block;
