import React, { Component } from 'react';

export default class Morale extends Component {
	state = {
		value: 0
	};

	handleChange = event => {
		this.setState({
			value: Number(event.target.value)
		});
	};

	changeMorale = () => {
		this.props.changeMorale(this.state.value, '+', this.props.index);
	};

	render() {
		return (
			<div style={{ marginTop: '1em', backgroundColor: this.props.color }}>
				<div>Morale: {this.props.morale}</div>
				<input
					type='number'
					value={this.state.value}
					onChange={this.handleChange}
				/>
				<button onClick={() => this.changeMorale()}>Add Morale</button>
			</div>
		);
	}
}
